:root {
    --c0: #fff;
    --c1: #EB74F9;
    --c2: #270A4A;
    --c3: #204C7D;
    --c4: #580B65;
}
html { margin: 0; padding: 0; color: var(--c0); }
body { 
    margin: 0; padding: 0; width: 100vw; height: 100vh;
    background-image: url("bg-hayny.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-color: #333;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    align-items: center;
}
h1 { 
    color: var(--c1); 
    text-shadow: 5px 5px 5px black, 0 0 0.5em var(--c1);
}
#gameContainer {
    padding: 0 1em;
    border-radius: 0.5em;
    display: inline-block;
    background-image: linear-gradient(var(--c1),var(--c4));
    margin-left: auto;
    margin-right: auto;
    opacity: 0.8;
    box-shadow: 10px 10px 10px black;
    text-align: center;
}
#citation { position: absolute; top: calc(100vh - 3em); }
a { text-decoration: none; }
a:visited { color: var(--c0); }
a:hover { color: var(--c1); }
a:active { color: var(--c1); }